@import '../../Public/tools.scss';

.p-dashboard {
  background: $white;
  margin-top: 70px;
  padding-top: $spacing_200;
  padding-bottom: $spacing_200;
}
.p-dashboard__content {
  @include max-width;
}

.c-card.p-dashboard__total_progress {
  padding: 0;
}

.p-dashboard__budgets_wrapper {
  margin-top: $spacing_200;
}
.p-dashboard__h1 {
  margin: 0;
}

.p-dashboard__budgets {
  margin: auto;
  margin-top: $spacing_100;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.p-dashboard__add_budget {
  margin-top: $spacing_200;
  text-align: center;
}

@media (max-width: 60px) {
  .p-dashboard__h1 {
    text-align: center;
  }
  .p-dashboard__budgets {
    display: block;
  }
}



