@import '../../Public/tools.scss';

.p-add_account {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.p-add_account__content {
  text-align: center;
}
.p-add_account__button {
  color: $black;
  background: $dark_blue;
}
.p-add_account__spacer {
  height: 0;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}
.p-add_account__spacer--open {
  height: 600px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}



